@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: bisque;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.box1, .box2{
    width: 450px;
    height: 450px;
}

img{
    border-radius: 20px;
}

.homepage-text{
    width: 600px;
    font-size: 14pt;
    text-align: justify;
    line-height: 30px;
    font-family: 'Open Sans', regular;
}

h1{
    text-align: center;
    font-size: 56pt;
    font-family: 'Oswald', regular;
}

h2{
    text-align: center;
    font-size: 24pt;
    font-family: 'Oswald', regular;
}

.part1{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 40px;
}

.part2{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 40px;
}


@media (max-width: 768px) {


    .part1, .part2{
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .homepage-text{
        width: 300px;
    }

    .box1, .box2{
        width: 300px;
        height: 300px;
        margin: auto;
    }



}
.img{
    width: 250px;
    height: 250px;
    background-color: rgb(199, 109, 0);
    font-size: 24pt;
    -webkit-flex-basis: calc(50% - 250px);
            flex-basis: calc(50% - 250px); 
    margin: 20px;
    text-align: center;
    line-height: 250px;
    cursor: pointer;
    border-radius: 20px;
}

a{
    text-decoration: none;
    color: black
}

a:hover{
    color: white;
}

.a-link{
    padding: 100px;
}

.coffeeshops-header{
    text-align: center;
}

.coffeeshops{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}


@media (max-width: 768px) {

    .a-link{
        padding: 0px;
    }

    .coffeeshops{
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-left: 50px;
    }



}
iframe{
    width: 750px;
    height: 450px;
    display: block;
    margin: auto;
}

.contact-text{
    text-align: justify;
    margin: auto;
    width: 350px;
}


@media (max-width: 768px) {

    iframe{
        width: 400px;
        height: 300px;
    }

}
.companysignup{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 500px;
    margin: auto
}

.input{
    background-color: white;
}

.logo{
    width: 100px;
    height: 100px;
    margin: auto;
}

.company-header{
    font-size: 24pt;
}


@media (max-width: 768px) {

    .companysignup{
        width: 100%;
    }
    .input{
        width: 400px;
        margin: auto;
        left: 20px;
    }

    button{
        width: 400px;
        margin: auto;
        left: 20px;
    }

    .company-header{
        margin-top: 30px;
    }

}
#view, ul{
    display: none;
}


@media (max-width: 768px) {

#view{
    display: block;
    margin-top: 10px;
    cursor: pointer;
}

#view :hover{
    color: grey;
}


.ul{
    display: block;
    cursor: pointer;
}

li{
    cursor: pointer;
    list-style: none;
    padding-bottom: 5px;
}

li :hover{
    color: grey;
}

}
.footer{
    background-color: #97572b;
    padding: 20px;
    text-align: center;
    margin-top: 30px;
    color: white;
}

.footer-logo{
    margin: auto;
}
